import {Route} from "@slyte/router";
import { ProfileComp } from "/components/javascript/profile-comp";

class User extends  Route {
    fetch(){
       return {"user":this.$app.$ServiceProvider.getUser(JSON.parse(localStorage.getItem("auth")).empNo)};
    }
    render(data,params){
       return { 
         outlet : "#outlet2",
         component : ProfileComp
       }
    }
    static actions(){
        return {

        }
    }
}

export {User};

